body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.Main {
  grid-area: a;
  color: #02005c;
  position: relative;
}

.Main .top {
  display: flex;
  align-items: center;
  padding-left: 60px;
}

.Main .top .weather-icon {
  width: 240px;
  height: 240px;
}

.Main .top .weather-icon img {
  width: 100%;
  height: 100%;
}

.Main .top .weather-condition {
  text-align: center;
}

.Main .top .weather-condition .it-is-text {
  font-size: 22px;
  margin-bottom: -30px;
  margin-top: 150px;
}

.Main .top .weather-condition .weather-type {
  font-size: 120px;
  font-family: "Yusei Magic", sans-serif;
}

.Main .bottom {
  margin: auto;
  margin-top: 40px;
  max-width: 600px;
  padding-left: 60px;
}

.Main .bottom .location {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  font-size: 22px;
  padding: 0 10px 10px;
  border-bottom: 1px solid #02005c;
  font-weight: bold;
}

.Main .bottom .location span {
  font-size: 18px;
}

.Main .bottom .location .temperature {
  white-space: nowrap;
}

.Main .bottom .weather-data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Main .bottom .weather-data .single-data {
  width: 80%;
  display: flex;
  margin-bottom: 4px;
}

.Main .bottom .weather-data .single-data .spacer {
  flex: 1 1;
  border-bottom: 1px dashed #02005c;
  margin: 0 10px 2px;
}

.Main .bottom .weather-data .single-data .value {
  width: 20%;
}

.Main button {
  display: none;
}

.Sunny_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #fffeb3 100%);
}

.Cloudy_bg,
.Foggy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #b6c4cf 100%);
}

.Rainy_bg,
.Snowy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #60c3e5 100%);
}

.Stormy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #ffbf5f 100%);
}

@media (max-width: 1260px) {
  .Main {
    height: 100vh;
  }
  .Main button {
    position: absolute;
    top: 20px;
    right: 20px;
    display: inline-block;
    width: 100px;
    height: 36px;
    border-radius: 18px;
    border: none;
    background-color: white;
    color: #2b6fee;
    font-weight: bold;
  }
  .Main button:last-of-type {
    top: 76px;
  }
  .Main button:focus {
    outline: none;
  }
}

@media (max-width: 500px) {
  .Main {
    height: 95vh;
  }
  .Main .top {
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 0;
    padding-top: 24vw;
  }
  .Main .top .weather-icon {
    width: 26vw;
    height: 26vw;
  }
  .Main .top .weather-condition .it-is-text {
    font-size: 5vw;
    margin-bottom: -4.9vw;
    margin-top: 6vw;
  }
  .Main .top .weather-condition .weather-type {
    font-size: 23vw;
  }
  .Main .bottom {
    margin-top: 12vw;
    max-width: 80%;
    padding-left: 0;
  }
  .Main .bottom .location {
    margin-bottom: 8vw;
    font-size: 5vw;
    padding: 0 3vw 3vw;
    align-items: center;
  }
  .Main .bottom .location span {
    font-size: 4.5vw;
  }
  .Main .bottom .weather-data {
    font-size: 5vw;
    padding: 0 3vw;
  }
  .Main .bottom .weather-data .single-data {
    width: 100%;
    margin-bottom: 0;
    padding: 3vw 0 1vw;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: 1px solid rgba(3, 0, 93, 0.2);
  }
  .Main .bottom .weather-data .single-data .name {
    max-width: 40%;
  }
  .Main .bottom .weather-data .single-data .name::after {
    content: ":";
  }
  .Main .bottom .weather-data .single-data .spacer {
    display: none;
  }
  .Main .bottom .weather-data .single-data .value {
    width: auto;
  }
  .Main button {
    top: auto;
    right: auto;
    bottom: 0;
    width: 50vw;
    height: 8vh;
    font-size: 4vw;
    border-radius: 0px;
  }
  .Main button:last-of-type {
    top: auto;
    right: 0;
  }
  .Main button:last-of-type::before {
    content: "";
    position: absolute;
    height: 50%;
    width: 1px;
    background-color: #02005c3a;
    top: auto;
    right: 100%;
  }
}

@media (max-width: 500px) and (min-aspect-ratio: 49 / 100) {
  .Main .top {
    padding-top: 22vw;
  }
  .Main .top .weather-icon {
    width: 24vw;
    height: 24vw;
  }
  .Main .top .weather-condition .it-is-text {
    font-size: 4vw;
    margin-bottom: -5.9vw;
    margin-top: 4vw;
  }
  .Main .top .weather-condition .weather-type {
    font-size: 18vw;
  }
  .Main .bottom {
    margin-top: 8.5vw;
  }
  .Main .bottom .location {
    margin-bottom: 6vw;
    font-size: 4.2vw;
    padding: 0 3vw 3vw;
  }
  .Main .bottom .location span {
    font-size: 3.4vw;
  }
  .Main .bottom .weather-data {
    font-size: 4.2vw;
    padding: 0 2vw;
  }
  .Main .bottom .weather-data .single-data {
    padding: 2.5vw 0 1vw;
  }
}
