* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.Hourcard {
  display: -ms-grid;
  display: grid;
  grid-template: "a c d" 40px "b c d" 40px / -webkit-max-content 1fr -webkit-max-content;
  grid-template: "a c d" 40px "b c d" 40px / max-content 1fr max-content;
  color: #474747;
  padding: 0 10px;
  transition: all 0.2s ease;
  border-bottom: 1px solid rgba(71, 71, 71, 0.1);
}

.Hourcard:hover {
  background-color: rgba(71, 71, 71, 0.1);
}

.hour {
  grid-area: a;
  align-self: end;
  font-weight: 800;
  font-size: 20px;
  display: flex;
}

.hour span {
  font-size: 12px;
  margin-bottom: 18px;
  margin-left: 3px;
}

.weather-description {
  grid-area: b;
  font-size: 16px;
  margin-top: 10px;
}

.weather-temperature {
  grid-area: c;
  -ms-grid-row-align: center;
  align-self: center;
  justify-self: flex-end;
  margin-right: 20px;
  font-size: 16px;
  font-weight: 600;
  position: relative;
}

.weather-temperature::after {
  content: "";
  position: absolute;
  right: -12px;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #474747;
}

.weather-icon {
  grid-area: d;
  -ms-grid-row-align: center;
  align-self: center;
  width: 40px;
  height: 40px;
}

.weather-icon img {
  width: 40px;
  height: 40px;
}

.hour,
.weather-description,
.weather-temperature,
.weather-icon {
