  height: 100%;
}

.Weekly .weekly-forecast {
  position: absolute;
  bottom: 100%;
  left: 0;
  width: calc(100% / 7);
  padding: 6px 0;
  border-top-right-radius: 10px;
  background-color: #474747;
  text-align: center;
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.Weekly .weekly-forecast img {
  display: none;
}

