    z-index: 1;
    transition: all 0.5s ease-out;
  }
  .Weekly .weekdays {
    overflow-x: visible;
    overflow-y: hidden;
  }
  .Weekly .weekly-forecast {
    width: 220px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
