* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.Loadingpage {
  width: 100%;
  height: 100vh;
  background-color: #60c3e5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Loadingpage .images {
  width: 100vw;
  height: 200px;
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
}

.Loadingpage .images img {
  position: absolute;
  top: 0;
  left: -100%;
  width: 200px;
  height: 200px;
}

.Loadingpage .images img.right {
  left: 100%;
}

.Loadingpage .images img.left {
  left: -100%;
  animation: swipe-left 3s ease;
}

.Loadingpage p {
  color: white;
  font-size: 30px;
  font-weight: bold;
}

@keyframes swipe-left {
  0% {
    left: 100%;
  }
  20% {
    left: calc(50% - 100px);
  }
  30% {
    left: calc(50% - 100px);
  }
  100% {
    left: -100%;
  }
}

@media (max-width: 500px) {
  .Loadingpage p {
    font-size: 8vw;
    text-align: center;
  }
}
/*# sourceMappingURL=Loadingpage.css.map */
